#errorMassege {
  border-radius: 10px;
  padding: 20px;
  position: relative;
  right: 320px;
  bottom: 380px;
  background: red;
  color: #fff;
 }
 #errorMassege::after, #errorMassege::before {
  content: '';
  position: absolute;
  background: red;
  right: -3px; bottom: 14px;
  width: 20px; height: 20px;
  z-index: 3; /* Прячем за основным блоком */
  transform: rotate(45deg); /* Поворачиваем на 45º */
  -webkit-transform: rotate(45deg);
 }
 #errorMassege::before {
  z-index: 1; /* Накладываем поверх, чтобы скрыть следы тени */
  box-shadow: none; /* Прячем тень */
 }

